body {
  margin: 0 auto;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(#e3ffe7), to(#d9e7ff));
  background: -webkit-linear-gradient(left, #e3ffe7 0%, #d9e7ff 100%);
  background: -o-linear-gradient(left, #e3ffe7 0%, #d9e7ff 100%);
  background: linear-gradient(90deg, #e3ffe7 0%, #d9e7ff 100%);
  height: 100%;
}

.container {
  width: 50%;
  height: 100%;
  margin: 0 auto;
}

.textInput {
  padding-left: 10px;
}
h1 {
  font-size: 14pt;
  padding: 10px;
}
.grid-container {
  display: grid;
  grid-column-gap: 50px;
  grid-row-gap: 50px;
  grid-template-columns: 400px, 400px;
  grid-template-rows: 200px, 200px;
}

.item1 {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 1;
}
.item2 {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 1;
}
.item3 {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 2;
}



